.addstore {
  width: 230px;
  height: auto;
  border: 3px rgb(146, 82, 82) solid;
  background-color: rgb(255, 253, 224);
  border-radius: 20px;
  padding: 10px;
  line-height: 35px;
  font-weight: 500;
  margin-bottom: 20px;
}
.addstore #input {
  position: absolute;
  width: 50%;
  height: 25px;
  color: black;
  margin-top: 7px;
  border-radius: 10px;
}
.addstore .opendiv input {
  width: 80%;
  margin: 10px auto;
  border-radius: 10px;
  height: 25px;
  color: black;
}
.addstore .opendiv .btn {
  width: 70%;
  height: 25px;
  margin: 10px auto;
}
.addstore .btn {
  margin-right: 130px;
  width: 25%;
  height: 25px;
  border: none;
  border-radius: 10px;
  background-color: rgb(0, 0, 0);
  transition-duration: 0.4s;
  cursor: pointer;
  font-size: 15px;
  font-weight: 500;
  color: rgb(255, 255, 255);
  line-height: normal;
}
